@import 'variables';
@import 'stars';

body {
  display: flex;
  justify-content: center;
}

html {
  font-family: Roboto, serif;

  line-height: 18px;
}

.card {
  box-sizing: border-box;

  width: 200px;
  height: 408px;
  border-radius: $border-radius;
  background: $color-background;
  border: 1px solid $color-border;
  margin-top: 150px;

  &__image {
    padding: 32px 19px 19px;
    width: 160px;
    height: 134px;
    display: block;
  }

  &__info-name-products {
    color: $color-first;
    font-weight: 500;
    line-height: 18px;
    width: 166px;
    font-size: 12px;
    padding-left: 16px;
    padding-right: 16px;
    margin-top: 21px;
  }

  &__info-ratings {
    margin-top: 23px;
    padding-right: 16px;
    padding-left: 16px;
    width: 166px;
    height: 16px;
    gap: 4px;
  }

  &__info-product-id {
    padding-left: 16px;
    padding-right: 84px;
    font-size: 10px;
    font-weight: 400;
    color: $color-second;
    line-height: 4px;
    margin-top: -1px;
  }

  &__info-review {
    font-size: 10px;
    font-weight: 400;
    color: $color-first;
    text-align: right;
    margin-top: -18px;
  }

  &__info-price {
    margin-top: 22px;
    font-weight: 400;
    font-size: 12px;
    color: $color-second;
    padding-left: 16px;
    padding-right: 16px;
  }
  &__info-price-value {
    font-weight: 700;
    font-size: 16px;
    color: $color-first;
    text-align: right;
    margin-top: -30px;
    padding-right: 16px;
  }

  &__info-button-buy {
    display: flex;
    text-decoration: none;
    align-items: center;
    justify-content: center;

    background-color: $color-button;
    color: white;
    cursor: pointer;
    font-weight: 700;
    font-size: 14px;

    width: 166px;
    height: 40px;
    border-radius: $border-radius;
    margin-right: 16px;
    margin-left: 16px;
    margin-top: 3px;
  }

  &__info-button-buy:hover {
    background-color: $color-background;
    border: 1px solid $color-button;
    color: $color-button;
    border-radius: $border-radius;
  }
}
